import React from "react";

const InfoCard = ({ img, title, text, linkText, linkSrc }) => {
  return (
    <div className="infocard">
      <img src={img} alt="medium" />
      <h3 className="heading-3">{title}</h3>
      <small>{text}</small>
      <a className="infocard__link btn-frame btn-sm" href={linkSrc}>
        {linkText}
      </a>
    </div>
  );
};
export default InfoCard;
