import React, { useState } from "react";

function StorySlider() {
  const [activeIndex, setActiveIndex] = useState(0);
  const setIndex = (variable) => {
    setActiveIndex(activeIndex + variable);
  };
  const swipeLeft = () => {
    if (activeIndex > 0) setIndex(-1);
  };
  const swipeRight = () => {
    if (activeIndex < 2) setIndex(1);
  };

  return (
    <section className="storySlider">
      <div className="container">
        <div className="storySlider__inner">
          <h2 className="heading-2">
            Why people love to raise money with Bonfire
          </h2>
          <div className="carousel">
            <div
              className={`slide ${activeIndex === 0 ? "slide--active" : ``}`}
            >
              <img
                src="https://c.bonfireassets.com/images/1.jpg"
                alt="slider img"
              />
              <div className="slide__text">
                <h3 className="heading-3">online campaigns</h3>
                <p>
                  Our mobile-friendly campaign pages are built for fundraising.
                  Reach supporters with your customized web page to share the
                  story and raise money.
                </p>
              </div>
            </div>
            <div
              className={`slide ${activeIndex === 1 ? "slide--active" : ``}`}
            >
              <img
                src="https://c.bonfireassets.com/images/2.jpg"
                alt="slider img"
              />
              <div className="slide__text">
                <h3 className="heading-3">online campaigns</h3>
                <p>
                  Our mobile-friendly campaign pages are built for fundraising.
                  Reach supporters with your customized web page to share the
                  story and raise money.
                </p>
              </div>
            </div>
            <div
              className={`slide ${activeIndex === 2 ? "slide--active" : ``}`}
            >
              <img
                src="https://c.bonfireassets.com/images/3.jpg"
                alt="slider img"
              />
              <div className="slide__text">
                <h3 className="heading-3">online campaigns</h3>
                <p>
                  Our mobile-friendly campaign pages are built for fundraising.
                  Reach supporters with your customized web page to share the
                  story and raise money.
                </p>
              </div>
            </div>
            <div className="controls">
              {activeIndex > 0 && (
                <span className="arrow arrow--left" onClick={swipeLeft}>
                  <img
                    src="https://c.bonfireassets.com/images/partials/icons/slider-arrow.svg"
                    alt="swipeLeft"
                  ></img>
                </span>
              )}
              {activeIndex < 2 && (
                <span className="arrow arrow--right" onClick={swipeRight}>
                  <img
                    src="https://c.bonfireassets.com/images/partials/icons/slider-arrow.svg"
                    alt="swipeRight"
                  ></img>
                </span>
              )}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
export default StorySlider;
